.slider,
.slider-mirrored {
    max-width: 100%
}

.slider__text {
    margin-right: -30px;
    position: relative;
    z-index: 999
}

.slider__item {
    display: -webkit-box !important;
    display: -ms-flexbox !important;
    display: flex !important;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between
}

.slider__link {
    display: block;
