    cursor: pointer;
    opacity: 0.4;
    border-radius: 50%;
    padding: 0
}

.slick-prev,
.slick-next {
    border: none;
    background-color: transparent;
    width: 22px;
    height: 22px;
    padding: 0;
    cursor: pointer;
    bottom: -6px
}

.slick-prev {
    position: absolute;
    right: 7%
}

.slick-next {
    position: absolute;
    right: 0
}

.slick-slide {
    width: 100%
}

.slick-slide img {
    margin-bottom: 35px
}

.slider-mirrored__item {
    display: -webkit-box !important;
